.navbar {
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar__links {
  display: none;
  color: #4F9CF9;
}
@media (min-width: 79em) {
  .navbar__links {
    display: block;
  }
  .navbar__links li {
    display: inline-block;
    position: relative;
    margin-right: 2rem;
  }
}
.navbar__links .navbar__link {
  position: relative;
}
.navbar__links .navbar__link:after {
  content: "";
  display: inline-block;
  background: url("../icons/down-arrow.svg") no-repeat center;
  background-size: contain;
  width: 1.5rem;
  height: 1.5rem;
  margin-left: 0.15rem;
  position: relative;
}
.navbar__dropdown {
  position: absolute;
  display: none;
  z-index: 1;
  background-color: #4F9CF9;
}
.navbar__dropdown--show {
  display: block;
}
.navbar__dropdown a {
  padding: 0.5rem 0.5rem;
  display: block;
}
.navbar__buttons {
  display: flex;
  align-items: center;
}
.navbar__buttons button {
  margin-inline: 0.625rem;
}
.navbar__cta {
  display: none;
}
@media (min-width: 48.0625em) {
  .navbar__cta {
    display: block;
  }
}
@media (min-width: 79em) {
  .navbar__hamburger-menu--disable {
    display: none;
  }
}

