.card {
  max-width: 29.375rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 1.25rem 1.25rem;
  border-radius: 0.625rem;
  border: 1px solid #FFE492;
}
@media (min-width: 48.0625em) {
  .card {
    margin: 1.5625rem;
    padding: 2.5rem 2.75rem;
  }
}
.card__service {
  display: flex;
  flex-direction: column;
  gap: 1.5625rem;
}
.card__title {
  font-size: 1.5rem;
  font-weight: normal;
}
.card__cost {
  font-size: 2.125rem;
}
.card .btn-secondary:after {
  content: none;
}
.card__benefit {
  position: relative;
  margin-top: 1.5625rem;
  margin-bottom: 1.5625rem;
  display: flex;
  align-items: center;
  gap: 1.1875rem;
}
.card__benefit:before {
  content: "";
  position: relative;
  display: inline-block;
  min-width: 1.5rem;
  min-height: 1.5rem;
  mask: url("../icons/check.svg");
  -webkit-mask: url("../icons/check.svg");
  background-color: #212529;
}

.review__card {
  flex-direction: column;
  align-items: flex-start;
  max-width: 30rem;
  min-height: 31.25rem;
  flex-wrap: wrap;
  gap: 2.1875rem;
  padding: 1.25rem 1rem;
  border-radius: 0.625rem;
  -webkit-box-shadow: 17px 26px 15px -7px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 17px 26px 15px -7px rgba(0, 0, 0, 0.35);
